@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=Manrope:wght@400;500;600;700;800&display=swap');

@layer base {
  html {
    scroll-behavior: smooth;
    overflow-x: hidden;
    max-width: 100vw;
  }
  
  body {
    font-family: 'Inter', sans-serif;
    line-height: 1.6;
    color: #e5e7eb;
    background-color: #030303;
    margin: 0;
    overflow-x: hidden;
    max-width: 100vw;
  }

  h1, h2, h3, h4, h5, h6 {
    font-family: 'Manrope', sans-serif;
    color: #0F172A;
    font-weight: 600;
  }
}

@layer components {
  .btn-primary {
    @apply bg-electric-blue hover:bg-blue-600 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 hover:shadow-medium;
  }
  
  .btn-secondary {
    @apply bg-ai-purple hover:bg-purple-600 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 hover:shadow-medium;
  }
  
  .btn-outline {
    @apply border-2 border-electric-blue text-electric-blue hover:bg-electric-blue hover:text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300;
  }
  
  .card {
    @apply bg-white rounded-xl shadow-soft hover:shadow-medium transition-all duration-300 transform hover:-translate-y-2;
  }
  
  .section-padding {
    @apply py-16 lg:py-24;
  }
  
  .container-custom {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }
  
  .gradient-text {
    @apply bg-gradient-to-r from-electric-blue to-ai-purple bg-clip-text text-transparent;
  }
  
  .gradient-bg {
    @apply bg-gradient-to-br from-deep-blue via-blue-900 to-ai-purple;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #F1F5F9;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #3B82F6, #6D5DF6);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(to bottom, #2563EB, #5B21B6);
}

/* Smooth animations */
* {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}